@keyframes ltr {

    from{
        translate: -5px 0 0;
    }
    to{
        translate: 5px 0 0;
    }
    
}

@media screen and (max-width : 990px) {

    .Loginpage {

        .loginpage-wrap {
            width: 100%;
            height: 100%;
            flex-direction: column;

            .left-wrap {
                height: 100px;
                width: 100%;
                justify-content: center;
                align-items: center;
                overflow: hidden;
                padding: 15px 0;



                .left-details {
                    display: none;

                }
            }

            .right-wrap {
                width: 100%;
                height: 100%;
                margin: 35px 0;

                form {
                    margin: 60px;

                    .row {
                        width: 90%;
                    }
                }
            }
        }
    }



}