.Eventmodal {
    width: 100%;
    height: 100vh;
    background: rgba(16, 12, 12, 0.677);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--secondary-font);

    .cust-heading {
        color: rgba(162, 162, 162, 0.56);
        display: inline;
        padding: 7px 0;
        position: relative;
        
        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 50%;
            height: 2px;
            background: rgba(255, 215, 71, 0.638);

            
        }
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            right: 0;
            width: 50%;
            height: 2px;
            background: rgba(255, 215, 71, 0.638);
        }
    }

    .event-modal-wrap {
        background: rgb(49, 48, 52);
        width: 90%;
        height: 90%;
        padding: 0 20px 20px 20px;
        border-radius: 24px;
        overflow-x: hidden;
        overflow-y: auto;
        position: relative;

        &::-webkit-scrollbar {
            width: 5px;
            height: 90%;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            // box-shadow: inset 0 0 5px grey; 
            border-radius: 10px;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: rgba(103, 103, 103, 0.416);
            border-radius: 10px;
            height: 200px;
        }

        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: #d3d3d3;
        }

        // close btn styling

        .close-mod-btn {
            width: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            position: sticky;
            top: 0;
            left: 0;
            background: rgb(49, 48, 52);
            padding: 10px 0;

            button {
                width: 40px;
                height: 40px;
                background: transparent;
                border: none;
                outline: none;
                font-size: 30px;
                color: aliceblue;
                transition: 0.5s ease;
                transform-origin: center;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;


                &:hover {
                    rotate: 90deg;
                    transition: 0.5s ease;
                }
            }
        }

        // Introduction section styling
        .introduction {
            width: 100%;


            p {
                color: white;
                text-align: justify;
                margin-top: 40px;

            }


        }


        // Basic information section
        .basic-info {
            margin-top: 40px;

          

            .rules {
                color: white;

                h2 {
                    color: white;
                    margin-top: 20px;

                }

                ul {
                    display: flex;
                    justify-content: flex-start;
                    align-items: flex-start;
                    list-style: none;
                    gap: 10px;
                    flex-direction: column;
                    margin-top: 20px;
                }
            }


        }

        // Round Details
        .round-details {
            h2 {
                color: white;
                margin-top: 20px;

            }

            p {
                color: white;
                text-align: justify;
                margin-top: 20px;

            }

        }

        // Fee Details
        .fees-details {
            h3 {
                color: white;


            }

            h2 {
                color: white;
                margin: 20px 0;


            }

        }


    }
}